export const buildHostListParams = () => {
    // TODO: append filter
    return {
        output: [
            // . ( ) ID  .
            'hostid',
            // .    .
            'host',
            // .    .
            //  :   host.
            'name',
            // .   .
            'description',
            //  .     .
            //  :
            // 0 - ( )    ;
            // 1 -    .
            'status',
            //  . ( )   .
            //  :
            // 0 - ( )  ;
            // 1 -   .
            'maintenance_status',
            //  .      .
            //  :
            // -1 - ( ) ;
            // 0 - ;
            // 1 - .
            'inventory_mode'
        ],
        selectInventory: [
            // Position lat&lng
            'location_lat',
            'location_lon'
        ],
        selectTriggers: [
            'triggerid',
            'state',
            'status',
        ],
    };
};

/**
 * @param {number} hostId
 * @param {{}} data
 * @return {{}}
 */
export const buildHostUpdateParams = (hostId, data) => {
    return Object.assign({}, data, {
        hostid: hostId,
    });
};
