/**
 * Trigger model.
 */
class Trigger {

    /**
     * ID .
     * @type {number}
     */
    id;

    /**
     *  .
     *  :
     * 0 - ( )     ;
     * 1 -    .
     * @type {number}
     */
    state;

    /**
     *    .
     *  :
     * 0 - ( ) ;
     * 1 - .
     *
     * @type {number}
     */
    status;

    /**
     * Update trigger data.
     *
     * @param data
     */
    update(data) {
        if (!this.id) {
            this.id = parseInt(data['triggerid'] || 0);
        }

        this.state = parseInt(data['state'] || 0);
        this.status = parseInt(data['status'] || 0);
    }

}

export default Trigger;